.App {
  padding: 0rem 0rem;
  padding-bottom: 50rem;
  text-align: center;
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(8,41,56,1) 100%);
  color: white;

}

.App_head {
  font-size: 30px;
  padding: 0.5rem 5rem;
}

.App_stock {
  justify-content: space-around;

  display: grid;
  grid-template-columns: auto auto auto;
}

.App_bar {
  display: flex;
  flex-direction: row;
  overflow-x: hidden;
}

.App_bar div {
  display: flex;
  flex-direction: row;
  animation: scroll 20s linear 0s infinite;
}

@keyframes scroll {
  0% {
    transform: translate(0%);
  }
  100% {
    transform: translate(-100%);
  }
}

@media screen and (max-width: 414px) {

  .App_head {
    font-size: 20px;
    padding: 0.5rem 0rem;
  }
  .App_bar div {
    display: flex;
    flex-direction: row;
    animation: scroll 5s linear 0s infinite;
  }

  .App_stock {
    padding: 0rem 4rem;
    justify-content: space-around;
  
    display: grid;
    grid-template-columns: auto ;
  }
}