.stock {
    width: 20rem;
    padding-bottom: 4rem;
}

.stock_head {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.stock_head h1 {
    font-size: 30px;
}

.stock_head div {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.stock_choice {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.stock_choice button {
    font-size: 20px;
    border: none;
    padding: 0.8rem 2rem;
    /* border-radius: 0.8rem; */
    cursor: pointer;
    color: white;
    font-weight: bold;
}

.green {
    background-color: green;
}

.red {
    background-color: rgb(255, 8, 0);
}

@media screen and (max-width: 414px) {
    .stock {
        width: 15rem;
        padding-bottom: 5rem;
    }
    .stock_head h1 {
        font-size: 25px;
    }
    .stock_choice button {
        font-size: 15px;
        border: none;
        padding: 1rem 2rem;
        /* border-radius: 0.8rem; */
        cursor: pointer;
        color: white;
        font-weight: bold;
    }
    
  }